@font-face{font-family:'Netflix Sans';font-weight:400;font-display:optional;src:url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Rg.woff2) format("woff2"),url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Rg.woff) format("woff")}*{margin:0;padding:0;border:0}*,*::before,*::after{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box}html{-webkit-text-size-adjust:100%;-moz-text-size-adjust:100%;text-size-adjust:100%}html,body{min-height:100vh;line-height:1;scroll-behavior:smooth}body{font-size:16px}h1,h2,h3,h4{font-size:inherit;font-weight:inherit}button{background-color:transparent;-webkit-user-select:none;-ms-user-select:none;user-select:none;color:inherit;border:0;padding:0;outline:none;font:inherit;cursor:pointer}img{vertical-align:top;max-width:100%;height:auto}ol,ul,li{list-style:none;margin:0}ul[class],ol[class]{padding:0;list-style:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}input[type=text]::-ms-clear{display:none;width:0;height:0}input[type=text]::-ms-reveal{display:none;width:0;height:0}input::-webkit-search-decoration,input::-webkit-search-cancel-button,input::-webkit-search-results-button,input::-webkit-search-results-decoration{display:none}input[type="text"],input[type="email"],input[type="tel"],input[type="number"],textarea{-webkit-appearance:none;appearance:none;outline:0;box-shadow:none}.observer-element{height:.52083vmax;background:transparent}.blockScroll{position:static;overflow-y:hidden}

